<template>
    <div class="container">
        <ul>
            <li @click="showBase = !showBase">
                <span class="icon">已选</span>
                <span
                    >{{ goods.title }} {{ initialSelect.conf }}
                    {{ initialSelect.color }} X {{ initialSelect.num }}</span
                >
                <van-icon name="arrow"></van-icon>
            </li>
            <li @click="showArea = true">
                <span class="icon">送至</span>
                <span> {{ area }} </span>
                <span>有现货</span>
                <van-icon name="arrow"></van-icon>
            </li>
            <li class="pro">
                <span>小米自营</span>
                <span>小米发货</span>
                <span>7天无理由退货</span>
            </li>
        </ul>

        <div>
            <van-image src="/images/detail/redmik20pro/detail01.jpg"></van-image>
            <van-image src="/images/detail/redmik20pro/detail02.webp"></van-image>
            <van-image src="/images/detail/redmik20pro/detail03.jpg"></van-image>
            <van-image src="/images/detail/redmik20pro/detail04.jpg"></van-image>
            <van-image src="/images/detail/redmik20pro/detail05.jpg" ></van-image>
        </div>
        <div class="more">
            查看更多参数 >
        </div>

        <!-- 配置选择 -->
        <van-sku
            v-model="showBase"
            :sku="sku"
            :goods="goods"
            :goods-id="goodsId"
            :initial-sku="initialSelect"
            @buy-clicked="onBuyClicked"
            @add-cart="onAddCartClicked"
            @sku-selected="skuSelected"
            @stepper-change="changeNum"
        />

        <!-- 地址选择 -->
        <transition mode="out-in">
            <van-area
                :area-list="areaList"
                @confirm="getArea"
                @cancel="showArea = false"
                v-if="showArea"
                style="position: fixed; bottom: 0;width: 100%; z-index: 99"
            />
        </transition>

        <!-- 底部按钮 -->
        <van-goods-action>
            <van-goods-action-icon icon="home-o" text="首页" @click="toHome"/>
            <van-goods-action-icon
                :info="num"
                icon="cart-o"
                text="购物车"
                @click="toCart"
            />
            <van-goods-action-button
                type="warning"
                text="加入购物车"
                @click="showBase = true"
            />
        </van-goods-action>
    </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import area from "@/assets/js/area.js";

export default {
    name: "detail-select",
    data() {
        return {
            goodsId: "10010", // 商品id
            goods: {
                // 商品标题
                title: "Redmi K20 Pro"
            },

            showBase: false, // 是否显示配置选择
            sku: {
                tree: [
                    // 规格
                    {
                        // 第一个分类
                        k: "颜色",
                        v: [
                            {
                                id: "101",
                                name: "火焰红",
                                imgUrl: "/images/detail/red.jpg"
                            },
                            {
                                id: "102",
                                name: "碳纤黑",
                                imgUrl: "/images/detail/black.webp"
                            },
                            {
                                id: "103",
                                name: "冰川蓝",
                                imgUrl: "/images/detail/blue.jpg"
                            }
                        ],
                        k_s: "s1"
                    },
                    {
                        // 第二个分类
                        k: "版本",
                        v: [
                            {
                                id: "201",
                                name: "6GB+64GB"
                            },
                            {
                                id: "202",
                                name: "6GB+128GB"
                            },
                            {
                                id: "203",
                                name: "8GB+128GB"
                            },
                            {
                                id: "204",
                                name: "8GB+256GB"
                            }
                        ],
                        k_s: "s2"
                    }
                ],

                list: [
                    // 所有的分类组合
                    {
                        id: "010100", // skuId，下单时后端需要
                        price: 249900, // 价格（单位分）
                        s1: "101", // 规格类目 k_s 为 s1 的对应规格值 id
                        s2: "201", // 规格类目 k_s 为 s2 的对应规格值 id
                        s3: "0", // 最多包含3个规格值，为0表示不存在该规格
                        conf: "6GB+64GB",
                        color: "火焰红",
                        img: "/images/detail/red.jpg",
                        stock_num: 110 // 当前 sku 组合对应的库存
                    },
                    {
                        id: "010200",
                        price: 259900,
                        s1: "101",
                        s2: "202",
                        s3: "0",
                        conf: "6GB+128GB",
                        color: "火焰红",
                        img: "/images/detail/red.jpg",
                        stock_num: 110
                    },
                    {
                        id: "010300",
                        price: 279900,
                        s1: "101",
                        s2: "203",
                        s3: "0",
                        conf: "8GB+128GB",
                        color: "火焰红",
                        img: "/images/detail/red.jpg",
                        stock_num: 110
                    },
                    {
                        id: "010400",
                        price: 299900,
                        s1: "101",
                        s2: "204",
                        s3: "0",
                        conf: "8GB+256GB",
                        color: "火焰红",
                        img: "/images/detail/red.jpg",
                        stock_num: 110
                    },
                    {
                        id: "020100",
                        price: 249900,
                        s1: "102",
                        s2: "201",
                        s3: "0",
                        conf: "6GB+64GB",
                        color: "碳纤黑",
                        img: "/images/detail/black.webp",
                        stock_num: 110
                    },
                    {
                        id: "020200",
                        price: 259900,
                        s1: "102",
                        s2: "202",
                        s3: "0",
                        conf: "6GB+128GB",
                        color: "碳纤黑",
                        img: "/images/detail/black.webp",
                        stock_num: 110
                    },
                    {
                        id: "020300",
                        price: 279900,
                        s1: "102",
                        s2: "203",
                        s3: "0",
                        conf: "8GB+128GB",
                        color: "碳纤黑",
                        img: "/images/detail/black.webp",
                        stock_num: 110
                    },
                    {
                        id: "020400",
                        price: 299900,
                        s1: "102",
                        s2: "204",
                        s3: "0",
                        conf: "8GB+256GB",
                        color: "碳纤黑",
                        img: "/images/detail/black.webp",
                        stock_num: 110
                    },
                    {
                        id: "030100",
                        price: 249900,
                        s1: "103",
                        s2: "201",
                        s3: "0",
                        conf: "6GB+64GB",
                        color: "冰川蓝",
                        img: "/images/detail/blue.jpg",
                        stock_num: 110
                    },
                    {
                        id: "030200",
                        price: 259900,
                        s1: "103",
                        s2: "202",
                        s3: "0",
                        conf: "6GB+128GB",
                        color: "冰川蓝",
                        img: "/images/detail/blue.jpg",
                        stock_num: 110
                    },
                    {
                        id: "030300",
                        price: 279900,
                        s1: "103",
                        s2: "203",
                        s3: "0",
                        conf: "8GB+128GB",
                        color: "冰川蓝",
                        img: "/images/detail/blue.jpg",
                        stock_num: 110
                    },
                    {
                        id: "030400",
                        price: 299900,
                        s1: "103",
                        s2: "204",
                        s3: "0",
                        conf: "8GB+256GB",
                        color: "冰川蓝",
                        img: "/images/detail/blue.jpg",
                        stock_num: 110
                    }
                ],

                stock_num: 2272 // 总库存
            },

            // 初始选择配置组合
            initialSelect: {
                s1: "101",
                s2: "201",
                color: "火焰红",
                conf: "6GB+64GB",
                num: 1,
                goodsId: "10010010100",
                goodsName: "Redmi K20 Pro",
                price: "249900",
                img: "/images/detail/red.jpg"
            },

            // 省市区
            areaList: area,
            // 显示地址选择
            showArea: false,
            // 选择的地区
            area: "北京市 东城区"
        };
    },
    methods: {
        ...mapMutations(["addGoods"]),

        // 立即购买
        onBuyClicked(data) {
            let obj = {};
            obj.goodsId = data.goodsId + data.selectedSkuComb.id;
            obj.goodsName = this.goods.title;
            obj.price = data.selectedSkuComb.price;
            obj.num = data.selectedNum;
            obj.img = data.selectedSkuComb.img;
            obj.color = data.selectedSkuComb.color;
            obj.conf = data.selectedSkuComb.conf;

            this.addGoods(obj);
            this.showBase = false;
            this.toCart();
        },

        // 加入购物车
        onAddCartClicked(data) {
            let obj = {};
            obj.goodsId = data.goodsId + data.selectedSkuComb.id;
            obj.goodsName = this.goods.title;
            obj.price = data.selectedSkuComb.price;
            obj.num = data.selectedNum;
            obj.img = data.selectedSkuComb.img;
            obj.color = data.selectedSkuComb.color;
            obj.conf = data.selectedSkuComb.conf;

            this.addGoods(obj);
            this.showBase = false;
            this.$toast.success("成功加入购物车!");
        },

        // 前往购物车页面
        toCart() {
            this.$router.push({ path: "/cart" });
        },

        // 前往首页
        toHome() {
            this.$router.push("/home");
        },
        
        // 选择的配置
        skuSelected(data) {
            this.initialSelect.goodsId = this.goodsId + data.selectedSkuComb.id;
            this.initialSelect.goodsName = this.goods.title;
            this.initialSelect.img = data.selectedSkuComb.img;
            this.initialSelect.price = data.selectedSkuComb.price;
            this.initialSelect.color = data.selectedSkuComb.color;
            this.initialSelect.conf = data.selectedSkuComb.conf;
        },

        changeNum(data) {
            this.initialSelect.num = data;
        },

        // 选择地址
        getArea(data) {
            this.area = "";
            if (data[0].name === data[1].name) {
                this.area += data[0].name + " ";
                this.area += data[2].name;
            } else {
                for (let i = 0; i < data.length; i++) {
                    this.area += data[i].name + " ";
                }
            }
            this.showArea = false;
        }
    },

    computed: {
        ...mapState(["cartGoods"]),

        // 自动计算购物车商品数量
        num() {
            let num = 0;
            this.cartGoods.forEach(item => {
                num += item.num;
            });
            return num;
        }
    }
};
</script>

<style lang="scss" scoped>
.van-image {
    display: block;
}
.more {
    color: #fff;
    background-color: #000;
    padding: 20px 13px;
    font-size: 18px;
}

.container {
    margin-bottom: 50px;
    border: 1px solid #eee;
    border-radius: 10px;
    font-size: 12px;
    ul {
        background-color: #fff;
        margin: 10px;
        border-radius: 5px; 
        li {
            padding: 10px;
            border-bottom: 1px solid #eee;

            &::after {
                content: "";
                display: block;
                clear: both;
            }

            &.pro {
                border-bottom: none;
            }

            span {
                float: left;
                margin-right: 5px;
                &.icon {
                    color: #ccc;
                    margin-right: 10px;
                }
            }

            .van-icon {
                color: #ccc;
                float: right;
            }
        }
    }
}


.v-enter,
.v-leave-to {
    opacity: 0;
    transform: translateY(100%);
}
.v-enter-active,
.v-leave-active {
    transition: all 0.5s ease;
}
</style>

