<template>
    <div class="container">
        <!-- 顶部轮播图 -->
        <van-swipe :autoplay="0" :loop="loop" indicator-color="white">
            <van-swipe-item>
                <van-image src="/images/detail/xiaomi801.webp"></van-image>
            </van-swipe-item>
            <van-swipe-item>
                <van-image src="/images/detail/xiaomi802.webp"></van-image>
            </van-swipe-item>
            <van-swipe-item>
                <van-image src="/images/detail/xiaomi803.webp"></van-image>
            </van-swipe-item>
            <van-swipe-item>
                <van-image src="/images/detail/xiaomi804.webp"></van-image>
            </van-swipe-item>
        </van-swipe>

        <!-- 返回按钮 -->
        <div class="btn" @click="back">
            <van-icon name="arrow-left"></van-icon>
        </div>
    </div>
</template>


<script>
export default {
    name: "detail-swiper",
    data() {
        return {
            loop: false
        };
    },
    methods: {
        // 返回上一页
        back() {
            if (window.history.length > 1) {
                this.$router.back();
            }
        }
    }
};
</script>

<style lang="scss" scoped>
.container {
    position: relative;
    .btn {
        position: absolute;
        top: 10px;
        left: 10px;
        display: inline-block;
        width: 30px;
        height: 30px;
        line-height: 30px;
        text-align: center;

        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 50%;
        color: #fff;
    }
}
</style>
